import * as React from 'react';
import Registro from '../registro/Registro';
import Cuestionario from '../cuestionario/Cuestionario';
import AgendaCitas from '../agenda-citas/AgendaCitas';

export function Entrevista() {
    const [completeStep, setCompleteStep] = React.useState({
        registro: false,
        cuestionario: false,
        agendaCita: false
    });

    function ShowStep() {
        if (!completeStep.registro) {
            return <Registro CompleteStep={completeStep} onFinishStep={setCompleteStep} />;
        }
        if (!completeStep.cuestionario) {
            return <Cuestionario CompleteStep={completeStep} onFinishStep={setCompleteStep} />;
        }
        if (!completeStep.agendaCita) {
            return <AgendaCitas />;
        }
    }

    // return <AgendaCitas />;
    return <ShowStep />;
}