import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import { areIntervalsOverlapping, setHours, isWithinInterval } from 'date-fns';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import TimePicker from '@mui/lab/TimePicker';
import {
    Checkbox,
    FormControlLabel,
    FormGroup,
    Grid,
    LinearProgress,
    Radio,
    RadioGroup,
    TextField,
    Typography
} from '@mui/material';
import SvgIcon from '@mui/material/SvgIcon';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { LoadingButton } from '@mui/lab';

function QuestionTiempo({ CuestData, onCuestDataChange }) {
    const [loading, setLoading] = React.useState(false);

    const validateBtnNext = (situation, timeStart, timeEnd) => {
        if (situation === 'd') {
            return true;
        } else if (
            (situation === 'e' || situation === 't')
            && timeStart !== null
            && timeEnd !== null
        ) {
            return true;
        } else {
            return false;
        }
    };

    const handleRadioSituation = (e) => {
        const valueS = e.target.value;
        onCuestDataChange({
            ...CuestData,
            timeWork: {
                ...CuestData.timeWork,
                situation: valueS,
                ready: validateBtnNext(valueS, CuestData.timeWork.timeStart, CuestData.timeWork.timeEnd)
            }
        });
    };

    const handleInputTimeStart = (e) => {
        const valueS = e;
        onCuestDataChange({
            ...CuestData,
            timeWork: {
                ...CuestData.timeWork,
                timeStart: valueS,
                ready: validateBtnNext(CuestData.timeWork.situation, valueS, CuestData.timeWork.timeEnd)
            }
        });
    };
    const handleInputTimeEnd = (e) => {
        const valueS = e;
        onCuestDataChange({
            ...CuestData,
            timeWork: {
                ...CuestData.timeWork,
                timeEnd: valueS,
                ready: validateBtnNext(CuestData.timeWork.situation, CuestData.timeWork.timeStart, valueS)
            }
        });
    };

    const handleSubmit = (resp) => {
        resp.preventDefault();
        // let timeStart = responseQ.timeStart;
        // let timeEnd = responseQ.timeEnd;
        // const intervalCheck = { 
        //     start: setHours(responseQ.timeStart, 9),
        //     end: setHours(responseQ.timeStart, 15)
        // };
        // // if () {

        // // }
        // if (
        //     areIntervalsOverlapping(
        //         { start: responseQ.timeStart, end: responseQ.timeEnd },
        //         intervalCheck,
        //         { inclusive: true }
        //     )
        // ) {
        //     if (
        //         isWithinInterval(responseQ.timeStart, intervalCheck)
        //         && isWithinInterval(responseQ.timeEnd, intervalCheck)
        //     ) {
        //         onCuestDataChange({
        //             ...CuestData,
        //             lineProgress: 100,
        //             timeWork: responseQ,
        //             msgEnd: "En estos momentos no tenemos un horario disponibles para atender tu candidatura."
        //         });
        //     }
        //     alert('Se sobreponen');
        // } else {
        //     onCuestDataChange({
        //         ...CuestData,
        //         lineProgress: 10,
        //         timeWork: responseQ
        //     });
        // }
        setLoading(true);
        setTimeout(() => {
            onCuestDataChange({
                ...CuestData,
                lineProgress: 10
            });
            setLoading(true);
        }, 1500);
    };

    return (
        <Box
            sx={{ px: { sm: 0, md: '10%' }, pt: '10vh' }}
            component='form'
            onSubmit={handleSubmit}
        >
            <Typography
                variant='h5'
                align='center'
                sx={{ mt: '10vh' }}
            >&#191;Est&aacute;s estudiando o trabajando actualmente?</Typography>
            {CuestData.timeWork.situation === '' &&
                <Typography
                    variant='body1'
                    align='center'
                >(Seleccione una opci&oacute;n para pasar a la siguiente)</Typography>
            }
            <Box
                display="flex"
                alignItems="center"
                justifyContent="center"
                sx={{ mt: '2vh' }}
            >
                <RadioGroup
                    id='radio-dedicaion'
                    aria-label="dedicacion"
                    name="radio-dedicacion"
                    value={CuestData.timeWork.situation}
                    onChange={handleRadioSituation}
                    row
                >
                    <FormControlLabel value="e" control={<Radio />} label="Estudio" />
                    <FormControlLabel value="t" control={<Radio />} label="Trabajo" />
                    <FormControlLabel value="d" control={<Radio />} label="No" />
                </RadioGroup>
            </Box>
            {(CuestData.timeWork.situation === 'e' || CuestData.timeWork.situation === 't') && (
                <Box>
                    <Typography
                        variant='h5'
                        align='center'
                        sx={{ mt: '7vh' }}
                    >&#191;En qu&eacute; horario?</Typography>
                    <Box
                        display="flex"
                        alignItems="center"
                        justifyContent="center"
                        sx={{ mt: '2vh' }}
                    >
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                            <TimePicker
                                value={CuestData.timeWork.timeStart}
                                onChange={handleInputTimeStart}
                                ampm={false}
                                renderInput={(params) => <TextField
                                    {...params}
                                    label="Desde"
                                    variant="standard"
                                    required
                                />}
                            />
                        </LocalizationProvider>
                        <Typography variant='h5' display='inline' sx={{ mt: 2, mx: 2 }}>A</Typography>
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                            <TimePicker
                                value={CuestData.timeWork.timeEnd}
                                onChange={handleInputTimeEnd}
                                ampm={false}
                                renderInput={(params) => <TextField
                                    {...params}
                                    label="Hasta"
                                    variant="standard"
                                    required
                                />}
                            />
                        </LocalizationProvider>
                    </Box>
                </Box>
            )}
            <Box sx={{ mt: '10vh' }}>
                <Typography align='center'>
                    <LoadingButton
                        loading={loading}
                        variant="contained"
                        disabled={!CuestData.timeWork.ready}
                        type="submit"
                        sx={{ borderRadius: 0 }}
                    >Siguiente</LoadingButton>
                </Typography>
            </Box>
        </Box>
    );
}

function QuestionInmediatDisponible({ CuestData, onCuestDataChange }) {
    const [loading, setLoading] = React.useState(false);

    const handleRadioAnswer = (e) => {
        const valueS = e.target.value;
        onCuestDataChange({
            ...CuestData,
            disponibleInmediat: {
                answer: valueS,
                ready: valueS !== ''
            }
        });
    };

    const handleAswer = () => {
        setLoading(true);
        setTimeout(() => {
            onCuestDataChange({
                ...CuestData,
                lineProgress: 25
            });
            setLoading(true);
        }, 1500);
    };

    // const handleButtonBack = (e) => {
    //     onCuestDataChange({
    //         ...CuestData,
    //         lineProgress: 0
    //     });
    // };

    return (
        <Box sx={{ px: { sm: 0, md: '10%' }, pt: '10vh' }}>
            <Typography
                variant='h5'
                align='center'
            >&#191;Tienes disponibilidad inmediata para comenzar a trabajar?</Typography>
            <Box
                display="flex"
                alignItems="center"
                justifyContent="center"
                sx={{ mt: '2vh' }}
            >
                <RadioGroup
                    id='radio-dedicaion'
                    aria-label="dedicacion"
                    name="radio-dedicacion"
                    value={CuestData.disponibleInmediat.answer}
                    onChange={handleRadioAnswer}
                    row
                >
                    <FormControlLabel value="s" control={<Radio />} label="Si" />
                    <FormControlLabel value='n' control={<Radio />} label="No" />
                    {CuestData.timeWork.situation === 't' &&
                        <FormControlLabel value="l" control={<Radio />} label="Luego de 15 d&iacute;as" />
                    }
                </RadioGroup>
            </Box>
            <Box sx={{ px: '10%', mt: '10vh' }} textAlign='center'>
                {/* <Button
                    sx={{ mr: '10%', borderRadius: 0 }}
                    color='inherit'
                    variant="contained"
                    onClick={handleButtonBack}
                >Atr&aacute;s</Button> */}
                <LoadingButton
                    loading={loading}
                    variant="contained"
                    disabled={!CuestData.disponibleInmediat.ready}
                    onClick={handleAswer}
                    sx={{ 
                        // ml: '10%', 
                        borderRadius: 0 }}
                >Siguiente</LoadingButton>
            </Box>
        </Box>
    );
}

