function QuestionEnd({ CuestData, onCuestDataChange, CompleteStep, onFinishStep }) {
    const [loading, setLoading] = React.useState(false);

    // const handleButtonBack = (e) => {
    //     onCuestDataChange({
    //         ...CuestData,
    //         lineProgress: 90
    //     });
    // };

    const handleCompleteQuest = (e) => {
        setLoading(true);
        setTimeout(() => {
            alert(JSON.stringify(CuestData));
            onFinishStep({
                ...CompleteStep,
                cuestionario: true
            });
            setLoading(false);
        }, 1000);
    };

    return (
        <Box>
            <Typography
                variant='h5'
                align='center'
                sx={{ mt: '10vh' }}
                gutterBottom
            >
                <SvgIcon sx={{ width: '10%', height: 'auto' }}><CheckCircleIcon color='success' /></SvgIcon>
            </Typography>
            <Typography
                variant='h5'
                align='center'
                gutterBottom
            >
                Ha conclu&iacute;do el Cuestionario de Compatibilidad
            </Typography>
            <Typography
                variant='subtitle1'
                align='center'
            >
                Si est&aacute; listo, proceda a agendar una cita para la entrevista presencial en nuestras oficinas.
            </Typography>
            <Box sx={{ px: '10%', mt: '10vh' }} textAlign='center'>
                <LoadingButton
                    loading={loading}
                    variant="contained"
                    onClick={handleCompleteQuest}
                    sx={{ borderRadius: 0 }}
                >Agendar Cita</LoadingButton>
            </Box>
        </Box>
    );
}

function ErrorStep({ CuestData, onCuestDataChange }) {
    const handleButtonBack = (e) => {
        onCuestDataChange(iniCuestData);
    };

    return (
        <Box>
            <Typography
                variant='h5'
                align='center'
            >
                Ups!!! Ha ocurrido un error.
            </Typography>
            <Box sx={{ mt: '5vh' }} textAlign='center'>
                <Button sx={{ mr: '10%' }} variant="outlined" onClick={handleButtonBack}>Regresar</Button>
            </Box>
        </Box>
    );
}

const iniCuestData = {
    lineProgress: 0,
    timeWork: { situation: '', timeStart: null, timeEnd: null, ready: false },
    disponibleInmediat: { answer: '', ready: false },
    disponibleWeek: { answer: '', ready: false },
    likeClientHelp: { answer: '', ready: false },
    workClientHelp: { answer: '', exp: [], extraCheck: false, extraExp: '', ready: false },
    socialNetwork: { socials: [], extraCheck: false, extraSocial: '', ready: false },
    sueldoIdeal: { answer: '', ready: false },
    workIdeal: { answer: '', ready: false },
    reasonNotWork: { answer: '', reason: '', ready: false },
    interviewMode: { answer: '', ready: false },
    longJob: { answer: '', ready: false }
};

export default function Cuestionario({ CompleteStep, onFinishStep }) {
    const [cuestData, setCuestData] = React.useState(iniCuestData);

    const showQuest = () => {
        switch (cuestData.lineProgress) {
            case 0:
                return (
                    <QuestionTiempo
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 10:
                return (
                    <QuestionInmediatDisponible
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 25:
                return (
                    <QuestionWeekDisponible
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 45:
                return (
                    <QuestionAtencionCliente
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 60:
                return (
                    <QuestionTrabajoAtenCliente
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 70:
                return (
                    <QuestionSocialNet
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 75:
                return (
                    <QuestionSueldo
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 80:
                return (
                    <QuestionWorkJobbie
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 85:
                return (
                    <QuestionNotWork
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 90:
                return (
                    <QuestionInterviewMode
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 95:
                return (
                    <QuestionLongJob
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 100:
                return (
                    <QuestionEnd
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                        CompleteStep={CompleteStep}
                        onFinishStep={onFinishStep}
                    />);
            
            default:
                return <ErrorStep
                    CuestData={cuestData}
                    onCuestDataChange={setCuestData}
                />;
        }
    };

    return (
        <Box
            sx={{
                width: { sm: '90%', md: '60%' },
                height: '100%',
                px: '10%',
                pt: '5%',
                pb: '5%',
                my: { sm: 0, md: '5vh' },
                mx: { sm: 0, md: '10%' },
                boxShadow: { sm: 0, md: 1 },
                // bgcolor: 'secondary.light'
            }}
        >
            <Box sx={{ width: '100%' }}>
                <LinearProgress variant="determinate" value={cuestData.lineProgress} />
                <Box sx={{ pt: 5 }} />
                {showQuest()}
            </Box>
        </Box>
    );
}