const ViewSelectDay = ({ AvailableDates, onDateInterview }) => {

    const getGroupDay = (dates) => {
        let dateArray = [];
        return dates.filter((date) => {
            const looking = dateArray.filter((dateX) => {
                return isSameDay(dateX, date.date);
            });
            if (looking.length === 0) {
                dateArray.push(date.date);
                return true;
            } else {
                return false;
            }
        });
    };

    return (
        <Box>
            <Typography variant='h5' align='center' sx={{ mb: '5vh' }}>SELECCIONE EL D&Iacute;A DE SU CITA</Typography>
            {getGroupDay(AvailableDates).map((date, index) => {
                return (<Button
                    key={index}
                    variant='contained'
                    fullWidth
                    sx={{
                        color: 'primary.contrastText',
                        borderRadius: 0,
                        py: 1,
                        my: '3vh',
                        textTransform: 'capitalize'
                    }}
                    onClick={() => {
                        onDateInterview(date.date);
                    }}
                >
                    <Typography variant='h6'>
                        {format(date.date, "EEEE',' MMMM d", { locale: es })}
                    </Typography>
                    <BookmarkAddedIcon fontSize='large' sx={{ ml: '5%' }} />
                </Button>);
            })}
        </Box>
    );
};

const ViewSelectHour = ({ AvailableDates, DateInterview, onDateCheck, onConfirmDate }) => {
    const [loading, setLoading] = React.useState(false);

    const getGroupHours = (availableDates, dateDay) => {
        return availableDates.filter((date) => {
            return isSameDay(dateDay, date.date);
        });
    };

    // chequear cita
    const handleConfirmDate = (date, e) => {
        setLoading(true);
        setTimeout(() => {
            onDateCheck(date.date);
            onConfirmDate(true);
            setLoading(false);
        }, 1000);
    }

    return (
        <Box>
            <Typography variant='h5' align='center'>SELECCIONE EL HORARIO DE SU CITA</Typography>
            <Typography variant='h4' align='center' sx={{ my: '5vh', textTransform: 'uppercase' }}>{format(DateInterview, "EEEE',' MMMM d", { locale: es })}</Typography>
            <Grid container spacing={2} alignItems="stretch">
                {getGroupHours(AvailableDates, DateInterview).map((date, index) => {
                    return (
                        <Grid item sm={4} xs={6} key={index}>
                            <LoadingButton
                                key={index}
                                variant='contained'
                                fullWidth
                                color={date.group ? 'secondary' : 'primary'}
                                loading={loading}
                                disabled={date.reserved || loading}
                                sx={{
                                    color: 'primary.contrastText',
                                    borderRadius: 0,
                                    py: 1,
                                    px: 3,
                                    textTransform: 'capitalize'
                                }}
                                onClick={(e) => handleConfirmDate(date, e)}
                            >
                                <AccessAlarmIcon fontSize='large' sx={{ mr: '10%' }} />
                                <Box>
                                    <Typography variant='h6'>
                                        {format(date.date, "HH':'mm")}
                                    </Typography>
                                    <Typography variant='body2'>
                                        {date.reserved ?
                                            'RESERVADA'
                                            : date.group ? 'Grupal'
                                                : 'Individual'}
                                    </Typography>
                                </Box>
                                {date.group ? (
                                    <PeopleIcon fontSize='small' sx={{ ml: '10%' }} />
                                ) : (
                                    <PersonIcon fontSize='small' sx={{ ml: '10%' }} />
                                )}
                            </LoadingButton>
                        </Grid>
                    );
                })}
            </Grid>
        </Box>
    );
};

const ViewConfirmDate = ({ DateInterview, onResetDate }) => {
    const [checkDate, setCheckDate] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    const handleReserveDate = () => {
        setLoading(true);
        setTimeout(() => {
            setCheckDate(true);
            setLoading(false);
        }, 1000);
    };

    return (
        <Box>
            {!checkDate ? (
                <React.Fragment>
                    <Typography variant='h5' align='center'>RESERVE SU CITA</Typography>
                    <Typography variant='h4' align='center' sx={{ my: '5vh', textTransform: 'uppercase' }}>{format(DateInterview, "EEEE',' MMMM d '-' HH':'mm", { locale: es })}</Typography>
                    <Box sx={{ mx: '10%' }}>
                        <Typography variant='body2'>Una vez realice su reserva, nuestro sistema le notificar&aacute; a trav&eacute;s de sus datos de contacto 24 horas antes, indic&aacute;ndole el procedimiento para que confirme o cancele su cita.</Typography>
                    </Box>
                    <Box sx={{ px: '10%', mt: '10vh' }} textAlign='center'>
                        <LoadingButton
                            loading={loading}
                            variant="contained"
                            color='success'
                            onClick={handleReserveDate}
                            sx={{ borderRadius: 0 }}
                        >Reservar</LoadingButton>
                    </Box>
                    <Box sx={{ mt: '5vh' }}>
                        <Stack direction='row' alignItems='center' justifyContent='center'>
                            <Typography variant='body2' align='center'>Si desea cambiar la fecha para su cita </Typography>
                            <Button
                                variant='text'
                                sx={{ textTransform: 'lowercase' }}
                                onClick={(e) => {
                                    onResetDate();
                                }}>Seleccione otra.</Button>
                        </Stack>
                    </Box>
                </React.Fragment>
            ) : (
                <React.Fragment>
                    <Typography variant='h5' align='center'>CITA RESERVADA</Typography>
                    <Stack direction='row' alignItems='center' justifyContent='center'>
                        <CheckCircleIcon color='success' sx={{ mr: { md: 2, sm: 1, xs: 0.6 }, width: { md: '5%', sm: '10%', xs: '15%' }, height: 'auto' }} />
                        <Typography variant='h4' align='center' sx={{ my: '5vh', textTransform: 'uppercase' }}>{format(DateInterview, "EEEE',' MMMM d '-' HH':'mm", { locale: es })}</Typography>
                    </Stack>
                    <Box>
                        <Typography align='center' variant='h6'>Informaci&oacute;n:</Typography>
                        <List
                            sx={{
                                width: '100%',
                                bgcolor: 'background.paper',
                                my: '5vh'
                            }}
                            disablePadding
                        >
                            <ListItem>
                                <ListItemAvatar>
                                    <PlaceIcon color="primary" />
                                </ListItemAvatar>
                                <ListItemText
                                    primary="Calle GASSET N&#176;10 ENTRESUELO, 12001 Castell&oacute;n de la Plana. Colindante con la Plaza de la Paz, justo al lado de &Oacute;PTICA VISUALIS."
                                />
                            </ListItem>
                            <Divider variant="inset" component="li" />
                            <ListItem>
                                <ListItemAvatar>
                                    <ContactPageIcon color="primary" />
                                </ListItemAvatar>
                                <ListItemText
                                    primary="Debe traer consigo el CURR&Iacute;CULUM VITAE, actualizado e impreso."
                                />
                            </ListItem>
                            <Divider variant="inset" component="li" />
                            <ListItem>
                                <ListItemAvatar>
                                    <CoronavirusIcon color="primary" />
                                </ListItemAvatar>
                                <ListItemText
                                    primary="Al llegar, el portal estar&aacute; abierto, por medidas ANTI-COVID deber&aacute;:"
                                />
                            </ListItem>
                            <Box sx={{ ml: { xs: '12%', sm: '12%', md: '8%' } }}>
                                <ListItem>
                                    <ListItemAvatar>
                                        <AccessTimeIcon color="primary" />
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary="Ser muy puntual."
                                    />
                                </ListItem>
                                <Divider variant="inset" component="li" />
                                <ListItem>
                                    <ListItemAvatar>
                                        <ReduceCapacityIcon color="primary" />
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary="No esperar en el portal, bajo ning&uacute;n concepto, evitando aglomeraci&oacute;n de personas."
                                    />
                                </ListItem>
                                <Divider variant="inset" component="li" />
                                <ListItem>
                                    <ListItemAvatar>
                                        <StairsIcon color="primary" />
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary="En cuanto llegue, suba directamente a nuestra oficina."
                                    />
                                </ListItem>
                            </Box>
                            <Divider variant="inset" component="li" />
                            <ListItem sx={{ display: { sm: 'none', xs: 'flex' } }}>
                                <ListItemAvatar>
                                    <InsertPhotoIcon color="primary" />
                                </ListItemAvatar>
                                <ListItemText
                                    primary="Foto del portal"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemAvatar sx={{ display: { sm: 'flex', xs: 'none' } }}>
                                    <InsertPhotoIcon color="primary" />
                                </ListItemAvatar>
                                <ListItemText sx={{ display: { sm: 'flex', xs: 'none' } }}
                                    primary="Foto del portal"
                                />
                                <Box
                                    component="img"
                                    sx={{
                                        height: 'auto',
                                        width: { xs: '100%', sm: '60%', md: '60%' }
                                    }}
                                    alt="Calle GASSET N&#176;10 ENTRESUELO, 12001 Castell&oacute;n de la Plana"
                                    src={portal}
                                    loading='lazy'
                                />
                            </ListItem>
                        </List>
                        <Typography variant='body2'>Esta informaci&oacute;n ha sido enviada a su correo electr&oacute;nico para que m&aacute;s tarde pueda consultarla.</Typography>
                    </Box>
                    <Box sx={{ px: '10%', mt: '10vh' }} textAlign='center'>
                        <Link to={'/'} style={{
                            textDecoration: 'none',
                            color: 'inherit'
                        }}>
                            <Button
                                variant="contained"
                                color='inherit'
                                sx={{ borderRadius: 0 }}
                            >Cerrar</Button>
                        </Link>
                    </Box>
                </React.Fragment>
            )}
        </Box>
    );
};

export default function AgendaCitas() {
    const [dateInterview, setDateInterview] = React.useState(null);
    const [confirmDate, setConfirmDate] = React.useState(false);

    const handleDateInterviewCheck = (date) => {
        setDateInterview(date);
        setConfirmDate(true);
    };

    const handleReset = () => {
        setDateInterview(null);
        setConfirmDate(false);
    };

    return (
        <Box
            sx={{
                width: { sm: '80%', md: '60%' },
                height: '100%',
                // px: '10%',
                px: { md: '10%', sm: '10%', xs: '5%' },
                pt: '5%',
                pb: '5%',
                my: { sm: 0, md: '5vh' },
                mx: { sm: 0, md: '10%' },
                boxShadow: { sm: 0, md: 1 }
            }}
        >
            <Box sx={{ width: '100%' }}>
                <Box sx={{ pt: 5 }} />
                {dateInterview === null ? (
                    <ViewSelectDay
                        AvailableDates={responseFakeDate}
                        onDateInterview={setDateInterview} />
                ) : (
                    !confirmDate ? (
                        <ViewSelectHour
                            AvailableDates={responseFakeDate}
                            DateInterview={dateInterview}
                            onDateCheck={handleDateInterviewCheck} />
                    ) : (
                        <ViewConfirmDate DateInterview={dateInterview} onResetDate={handleReset} />
                    )
                )}
            </Box>
        </Box>
    );
}